<script>
	//import VueAnime from 'vue-animejs';
	//Vue.use(VueAnime);
	import myMap from './map/Map.vue';

	export default {
		name: "contact",
		props: ['dataProps'],
		mounted() {
			this.$anime({
				targets: this.$el,
				bottom: 'unset',
				top: 0,
				duration: 500,
				easing: 'easeInOutSine'
			});
		},
