<template>
	<l-map :zoom="zoom" :center="center" :style="`height: ${mapHeight}px`">
		<l-tile-layer :url="url" :attribution="attribution"></l-tile-layer>
		<l-marker v-for="key in createMarkers"
				  :key="key.id" :lat-lng="key.marker">
			<l-popup v-if="key.popup" :content="key.popup"></l-popup>
		</l-marker>
	</l-map>
</template>
<style lang="scss">
	.leaflet-container{
		height: 500px;
	}
	@import "~leaflet/dist/leaflet.css";
</style>

<script>
	import {LMap, LTileLayer, LPopup, LMarker} from 'vue2-leaflet'
	export default {
		name: 'my-map',
		// props: ['map-props'],
		props: {
			mapProps: {
				type: Object,
			},
		},
		data() {
			return {
				zoom: this.mapProps.params.zoom,
				center: L.latLng(this.mapProps.params.center.longitude,
								this.mapProps.params.center.latitude),
				url:'http://{s}.tile.osm.org/{z}/{x}/{y}.png',
				attribution:'&copy; <a href="http://osm.org/copyright">OpenStreetMap</a> contributors',
				mapHeight: window.innerHeight / 2,
				oldMarkers: this.mapProps.markers
			}
		},
		computed: {
			createMarkers(){
				let arrMarkers = [];
				for(let i = 0; i < this.oldMarkers.length; i++) {
					let marker = L.latLng(this.oldMarkers[i].longitude, this.oldMarkers[i].latitude);
					arrMarkers.push({
						marker,
						popup: this.oldMarkers[i].popup
					});
				}
				return arrMarkers;
			}
		},
		components: { LMap,	LTileLayer, LPopup,	LMarker }
	}
</script>